
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Poppins', sans-serif;
}
.navbar{
    display: flex;
    align-items: center;
    padding: 20px;
    width: 88%;
margin: auto;
}
nav{
    flex: 1;
    text-align: right;
}
nav img{
    color: white;
}
nav ul{
    display: inline-block;
    list-style: none;
    z-index: 1;
    transition:0.5s ease;
}
nav ul li{
    display: inline-block;
    margin: 20px;
}
a{
    text-decoration: none;
    color: rgb(0, 0, 0);
}
p{
    color: rgb(0, 0, 0);
}
.searchform input[type=search] {
    font: 1em/1.618 Open Sans, Arial, Sans-serif;
    /* border: 2px solid rgb(255, 255, 255); */
    border-width: 0 0 1px;
    background-color: transparent;
    border-right: none;
    padding: .1875em .375em;
    width: 100%;
  }
  
  .searchform input[type=search]:focus {
    border-color: #ff523b;
    color: #ffffff;
    outline: 0;

    /* border: 2px solid rgb(255, 255, 255); */
    border-left: none;
  }
  
  #search-icon{
      border: px solid rgb(255, 255, 255);
      padding: 7px;
      color: white;
  }
  @media only screen and (min-width: 48em) {
    .searchform input[type=search]{ width: 50%; }
  }

.container{
    max-width: 100%;
    margin: auto;
    /* padding-left: 25px;
    padding-right:25px; */
}
.row{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 25px;
    justify-content: space-around;
    /* width: 88%;
    margin: auto; */
}
.col-2{
    flex-basis:  50%;
    min-width: 300px;
}

.col-2 img{
    max-width: 100%;
    padding: 50px 0;
}
.col-2 h1{
    font-size: 50px;
    line-height: 60px;
    margin: 25px 0;
}
.btn{
    display: inline-block;
    background: #ff523b;
    color: #fff;
    padding: 8px 30px ;
    margin: 30px 0;
    border-radius: 30px;
    transition: 0.5s;
}
.btn:hover{
    background: #e63c26;
}
.header{
    background: radial-gradient(#fff,#ffd6d6);
}
.header .row{
    /* margin-top: 20px; */
    padding: 0 70px;
}
/* [1] The container */
.img-hover-zoom {
    height: 300px; /* [1.1] Set it as per your need */
    overflow: hidden; /* [1.2] Hide the overflowing of child elements */
  }
  
  /* [2] Transition property for smooth transformation of images */
  .img-hover-zoom img {
    transition: transform .5s ease;
  }
  
  /* [3] Finally, transforming the image when container gets hovered */
  .img-hover-zoom:hover img {
    transform: scale(1.5);
  }

/* Featured Categories   */

.categories{
    margin: 70px 0;
}
.col-3{
    flex-basis: 30%;
    min-width: 250px;
    margin-bottom: 30px;
}
.col-3 img{
    width:100%;
}
.contact img{
    transform: scaleX(-1);
}
.small-container{
    max-width:1080px;
    margin:auto;
    padding-left: 25px;
    padding-right:25px;
}
.col-4{
    flex-basis: 25%;
    padding: 10px;
    min-width: 200px;
    margin-bottom: 50px;
    transition: transform 0.5s;
}
.col-4 img{
    width: 100%;
}
.col-4:hover{
    transform: translateY(-5px);
}
.title{
    text-align: center;
    margin: 0 auto 80px;
    position: relative;
    line-height: 60px;
    z-index: -1;
    color: #555;
}
.title::after{
    content: '';
    background: #ff523b;
    width: 80%;
    height: 5px;
    border-radius: 5px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

h4{
    color: #555;
    font-weight: normal;
}
.col-4 p{
    font-size: 14px;
}
.rating .fa{
    color: #ff523b;
}


/* Offer Section */
.offer{
    background: radial-gradient(#fff,#ffd6d6);
    margin-top: 80px;
    margin-bottom: 80px;
    padding-top:30px 0;
}
.col-2 .offer-img{
    padding: 50px;
}
small{
    color: #555;
}

/* Testimonial */
.testimonial{
    padding-top: 100px;
}
.testimonial .col-3{
    text-align:center;
    padding: 40px 20px;
    box-shadow: 0 0 20px 0px rgba(0,0,0,0.1);
    cursor: pointer;
    transition: transform 0.5s;
}
.testimonial .col-3:hover{
    transform: translateY(-1px);
}
.testimonial .col-3 img{
    width: 50px;
    margin-top: 20px;
    border-radius:50%;
}
.fa.fa-quote-left{
    font-size: 34px;
    color: #ff523b;
}
.col-3 p{
    font-size: 12px;
    margin: 12px 0 0;
    color: #777;
}
.testimonial .col-3 h3{
    font-weight: 600;
    font-size: 16px;
    color: #555;
}

/* contact  */
.contact .col-3{
    flex-basis: 50%;
}
.contact .small-container{
max-width: 1300px;
margin: auto;
padding-left: 25px;
padding-right: 0px;
}
.contact .main-details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact .main-details input{
    height: 38px;
    width: 60%;
    margin: 19px;
    padding-left: 15px;
}
.contact .main-details input:hover{
    border:1px solid #e63c26;
}
.contact .main-details input:focus{
    outline: none;
    border: 1px solid #e63c26;
}
#textarea{
    height: 125px;
    width: 60%;
    padding-left: 15px;
    padding-top: 10px;
    resize: none;
    margin: 25px;
    }

    #textarea:focus{
        outline: none;
        border: 1px solid #e63c26;
    }
    
    #textarea:hover{
        border: 1px solid #e63c26;
    }
.contact .send{
    width: 60%;
    border-radius: 0px;
    border: none;
    margin-top: 0px;
}

 /* media query for contact  */
 @media only screen and (max-width:800px){
     .contact .row{
         flex-direction: column;
     }
     .contact .main-details input {
        width: 90%;
    }
    .contact .small-container {
        padding-left:0 ;
        padding-right: 0;
    }
    .contact .main-details input {
        width: 120%;
    }
    #textarea {
        width: 120%;
    }
    .contact .send {
        width: 120%;
    }
 }

 
 @media only screen and (max-width:400px){
    .contact .main-details input {
        width: 100%;
    }
    #textarea {
        width: 100%;
    }
    .contact .send {
        width: 100%;
    }
 }

/* Brands  */
.brands{
    margin: 100px auto ;
}
.col-5{
    width: 160px;
}
.col-5 img{
    width: 100%;
    cursor: pointer;
    filter: grayscale(100%);
}
.col-5 img:hover{
    filter: grayscale(0);
}

/* Footer  */
.footer{
    background: #000;
    color: #8a8a8a;
    font-size: 14px;
    padding: 60px 0 20px ;
}
.footer p{
    color: #8a8a8a;
}
.footer h3{
    color: #fff;
    margin-bottom: 20px;
}
.footer-col-1 , .footer-col-2,.footer-col-3,.footer-col-4{
    min-width: 250px;
    margin-bottom: 20px;
} 
.footer-col-1{
    flex-basis: 30%;
}

.footer-col-2{
    flex: 1;
    text-align: center;
}
.footer-col-2 img{
    width: 180px;
    margin-bottom: 20px;
}

.footer-col-3 , .footer-col-4{
    flex-basis: 12%;
    text-align: center;
}
ul{
    list-style: none;
}
.app-logo{
    margin-top:20px;
}
.app-logo img{
    width: 140px;
}
.footer hr{
    border: none;
    background: #b5b5b5;
    height:1px;
    margin: 20px 0;
}
.footer .copyright{
    text-align: center;
}
.menu-icon{
    width: 28px;
    margin-left: 20px;
    display: none;
}


/* All Products  */
.row-2{
    justify-content: space-between;
    margin: 100px auto 50px;
}
select{
    border: 1px solid #ff523b;
    padding: 5px;
}
select:focus{
    outline:none;
}
.page-btn{
    margin: 0 auto 80px;
}
.page-btn span{
    display: inline-block;
    border: 1px solid #ff523b;
    margin-left: 10px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
}
.page-btn span:hover{
    background: #ff523b;
    color: white;
}


/* Single Product Details  */
.single-product{
    margin-top: 80px;
}
.single-product .col-2 img{
    padding: 0;
}
.single-product .col-2{
    padding: 20px;
}
.single-product h4{
    margin: 20px 0;
    font-size: 22px;
    font-weight: bold;
}
.single-product select{
    display: block;
    padding: 10px;
    margin-top: 20px;
}
.single-product input{
    width: 50px;
    height: 40px;
    padding-left: 10px;
    font-size: 20px;
    margin-right: 10px;
    border: 1px solid #ff523b;
}
.single-product input:focus{
    outline: none;
}

.single-product .fa{
    color: #ff523b;
}

.small-img-row{
    display: flex;
    justify-content: space-between;
}
.small-img-row .small-img-col{
    flex-basis: 24%;
    cursor: pointer;
}


/* CArt Items  */
.cart-page{
    margin: 80px auto;
}
table{
    width: 100%;
    border-collapse: collapse;
}
.cart-info{
    display: flex;
    flex-wrap: wrap;
}
th{
    text-align: left;
    padding: 5px;
    color: #fff;
    background: #ff523b;
    font-weight: normal;
}
td{
    padding: 10px 5px;
}
td input{
    width: 40px;
    height: 30px;
    padding: 5px;
}
td a{
    color: #ff523b;
    font-size: 12px;
}
td img{
    width: 80px;
    height: 80px;
    margin-right: 10px;
}
.total-price{
    display: flex;
    justify-content: flex-end;
}
.total-price table{
    border-top: 3px solid #ff523b;
    width: 100%;
    max-width: 410px;
}
td:last-child{
    text-align: right;
}
th:last-child{
    text-align: right;
}


/* Account Page  */
.account-page{
    padding: 50px 0;
    background:radial-gradient(#fff,#ffd6d6);
}
.form-container{           
    background: #fff;
    width: 300px;
    height: 400px;
    position: relative;
    text-align: center;
    padding: 20px 0;
    margin: auto;
    box-shadow: 0 0 20px 0px rgba(0,0,0,0.1);
    overflow: hidden;
}
.form-container span{
    font-weight: bold;
    padding: 0 10px;
    color: #555;
    cursor: pointer;
    width: 100px;
    display: inline-block;
}
.form-btn{
    display: inline-block;
}
#loginForm{
    display: none;
    max-width: 100%;
    padding: 0 20px;
    position: absolute;
    top: 130px;
    transition: transform 1s;
}

#regForm{
    max-width: 100%;
    padding: 0 20px;
    position: absolute;
    top: 130px;
    transition: transform 1s;
}
form input{
    width: 100%;
    height: 30px;
    margin: 10px 0;
    padding: 0 10px;
    border: 1px solid #ccc;
    /* border-radius: 23px; */
}
form a{
    font-size: 12px;
}
form .btn{
    width: 100%;
    border: none;
    cursor: pointer;
    margin: 10px 0 ;
}
form .btn:focus{
    outline: none;
}
/* #loginForm{
    left: -300px;
}
#regForm{
    left: 0;
} */
#indicator{
    width: 100px;
    border: none;
    background: #ff523b;
    margin-top: 8px;
    height: 3px;
    transform: translateX(100px);
    transition: transform 1s;
}
 /* media query for menu  */
 @media only screen and (max-width:800px){
     
    /* nav ul li{
        display: none;
    } */
    nav ul{
        position: absolute;
        top: -240px;
    }
    #MenuItems{
        display: flex;
        flex-direction: column;
         top: 70px;
         left: 0;
         background: #333;
         width: 100%;
         overflow: hidden;
         transition:0.5s ease;
     }
     nav ul li{
         margin-right: 50px;
         margin-top: 10px;
         margin-bottom: 10px;
     }
     nav ul li a{
         color: #fff;
     }
     .menu-icon{
         display: block;
     }
 }

 /* meida query for less than 600 px */
 @media only screen and (max-width:600px){
     .row{
         text-align:center;
     }
     .col-2,.col-3,.col-4{
         flex-basis: 100%;
     }
     .single-product .row{
         text-align: left;
     }
     .single-product .col-2{
         padding: 20px 0;
     }
     .single-product h1{
         font-size: 26px;
         line-height: 32px;
     }
     .cart-info p{
         display: none;
     }
 }